.hamberger .menu__button3 {
  margin: 0.5rem auto 0;
  position: relative;
  display: block;
  z-index: 1001;
  height: 2px;
  width: 32px;
  transition: ease 0.3s;
}

.js-hamberger.is-active {
  transition: 0.3s ease;
}

.top__drawer {
