}

.sp-nav {
  position: absolute;
  top: 4rem;
  left: 0;
  right: 0;
  transform: translateX(-100%);
  background-color: #007FC6;
  width: 100%;
  height: 100vh;
  overflow-y: scroll;
  transition: 0.3s ease;
  z-index: 999;
}

.js-hamberger.is-active .menu__button {
  right: 0.45em;
}

