  right: 0rem;
  transform: translateX(0);
}

.hamberger .menu:nth-child(1) {
  top: 1.1rem;
}

.js-hambergeris-active .menu__button2 {
  top: 0.9875rem;
}

.hamberger .menu:nth-child(2) {
  top: 1.25rem;
  margin: 0.1em auto;
}

.hamberger.is-active .menu__button:nth-child(1) {
  top: 0.65rem;
  transform: rotate(45deg);
}

.hamberger.is-active .menu__button:nth-child(2) {
  transform: translateY(-50%);
