.inquiry-form__baloon {
  position: relative;
  background-color: #007FC6; /* 吹き出しの背景色 */
  color: #ffffff; /* テキストの色 */
  padding: 1.125rem 1.25rem;
  margin: 1.5em 0;
  max-width: 100%;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 0.3125rem;
  border: 1px solid #000000; /* 吹き出しの枠線 */
}

.inquiry-form__baloon::before,
.inquiry-form__baloon::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  border: solid transparent;
  height: 0;
  width: 0;
  pointer-events: none;
}

.inquiry-form__baloon::before {
  top: 100%;
  border-width: 14px;
  border-top-color: #000000; /* 外側の黒線 */
}

.inquiry-form__baloon::after {
  top: calc(100% - 1px); /* 外側の三角形の内側に配置 */
  border-width: 14px;
  border-top-color: #007FC6; /* 吹き出しの背景色と同じ */
