  width: 2rem;
  border-radius: 50%;
  font-family: "Goldman", sans-serif;
  font-size: 1.5rem;
  font-weight: 700;
  text-align: center;
  margin-right: 0.625rem;
  color: #ffffff;
  transition: background-color 1s ease-out;
}
